(function() {
    var Highcharts = require('highcharts');
    var deepMerge = require('lodash.merge');

    require('highcharts/modules/data')(Highcharts);
    require('highcharts/modules/series-label')(Highcharts);
    require('highcharts/modules/exporting')(Highcharts);
    require('highcharts/modules/export-data')(Highcharts);
    require('highcharts/modules/drilldown')(Highcharts);

    var buildTheme = require('./../../_js-common-modules/highcharts').build;
    var colors = require('./../../_js-common-modules/colors');

    var dashboardHighcharts = {
        colorsScheme: { },
        setColorScheme: function(colorsScheme) {
            this.colorsScheme = colorsScheme;
        },
        setTheme: function(overrides) {
            var theme = Object.assign(
                { },
                buildTheme(this.colorsScheme),
                overrides
            );
            
            Highcharts.setOptions(theme);
        },
        createChart: function(chartType, config) {
            // If a scheme is not provided use colors from module
            if (Object.keys(this.colorsScheme).length === 0) {
                this.colorsScheme = colors;
            }

            var options = deepMerge(
                buildTheme(this.colorsScheme),
                config
            );
    
            Highcharts.chart(chartType, options);
        },
    };

    window.Dashboard = Object.assign({}, window.Dashboard);

    window.Dashboard.Highcharts = dashboardHighcharts;
})();
